import Modal from "react-bootstrap/Modal";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Image from "react-bootstrap/Image";
import Button from "react-bootstrap/Button";
import React from "react";
import "SplashScreen.css";
import {phone} from "./Globals";
import {DEFAULT_PLAYER_NAME} from "./App";
import scoochLogo from "scooch.png";

interface ISplashScreenComponentProps {
    playerName: string | undefined;

    changePlayerName?(value: string): void;

    solo(): void;
    challenge(): void;
}

export default class SplashScreenComponent extends React.Component<ISplashScreenComponentProps> {
    public render() {
        const {playerName, changePlayerName, solo, challenge} = this.props;
        return <Modal className={"splash"} backdrop={"static"} show={true} scrollable={false}>
            <div className={"top-spacer"}/>
            <Modal.Body>
                <Row>
                    <Col>
                        <Image src={scoochLogo}/>
                    </Col>
                </Row>
                <Row>
                    {phone() && <Col className="flex-grow"/>}
                    <Col>
                        {changePlayerName !== undefined && <Form.Group>
                            {!phone() && <Form.Label>Your Name</Form.Label>}
                            <Form.Control type="input" placeholder="Your Name"
                                          value={playerName === DEFAULT_PLAYER_NAME ? undefined : playerName}
                                          onChange={changeNameThunk}/>
                        </Form.Group>}
                    </Col>
                    {phone() && <Col className="flex-grow"/>}
                </Row>
            </Modal.Body>
                <Row>
                    <Col className="flex-grow"/>
                    <Col xs={8}>
                        <Row>
                            <Button variant="success" onClick={solo} style={{width: "100%"}}>Solo Play</Button>
                        </Row>
                    </Col>
                    <Col className="flex-grow"/>
                </Row>
                <Row>&nbsp;</Row>
                <Row>
                    <Col className="flex-grow"/>
                    <Col xs={8}>
                        <Row>
                            <Button variant="primary" onClick={challenge} style={{width: "100%"}}>Challenge</Button>
                        </Row>
                    </Col>
                    <Col className="flex-grow"/>
                </Row>
        </Modal>;

        function changeNameThunk(event: any) {
            if (changePlayerName !== undefined) {
                changePlayerName(event.target.value);
            }
        }
    }
}
