interface IFooterProps {
    playLabel: string | false;

    canReset: boolean;
    usedNuke: boolean;
    noMoreTiles: boolean;
    canPlay: boolean;
    canPass: boolean;
    passWillEndGame: boolean;

    menu(): void;

    reset(): void;

    nuke(): void;

    play(): void;

    pass(): void;
}

function ImageWithDisabledState(props: {
    src: string,
    onClick: () => void,
    title?: string,
    disabled: boolean,
    action: "play" | "nuke" | "pass" | "reset",
}) {
    const {action, disabled} = props;
    let onClick: undefined | (() => void) = props.onClick;
    if (disabled) {
        onClick = undefined;
    }
    return <Row className={classNames(disabled && "disabled", `action-${action}`)}>
        <Image {...props} onClick={onClick}/>
    </Row>;
}

